.education {
  width: 500px;
  background-color: white;
  height: 800px;
  margin-bottom: 2rem;
  border-radius: .4rem;
  box-shadow: 0px 0px 8px 1px #333;
}

.education > h1 {
  color: #333;
  text-align: center;
}

.education > p {
  color: #555;
  text-align: center;
}

.education > p::after {
  content: "";
  width: 2px;
  height: 520px;
  background: #333;
  display: block;
  position: relative;
  top: 70px;
  margin: 0 auto;
}

.education ul {
  position: relative;
  top: -440px;
  width: 100%;
}