.container {
  overflow: hidden;
}

.content {
  display: flex;
  transition: transform .3s ease;
}

.item {
  flex-shrink: 0;
  width: 80%;
  margin: 0 10%;
  padding: 1rem 0;
  border-radius: .4rem;
  text-align: center;
}

.nav {
  display: flex;
  justify-content: space-around;
  margin: 1rem auto;
  width: 80%;
}

.nav button {
  border: none;
  border-radius: .4rem;
  background: #333;
  color: white;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: bold;
}